@import "../common/variable";
@import "../common/mixins";

.hero {
  padding: r(168) 0 r(88);

  &__text {
    span {
      display: inline-block;
      -webkit-transform: translateY(50%);
          -ms-transform: translateY(50%);
              transform: translateY(50%);
      opacity: 0;
    }
  }

  &__description {
    margin-left: auto;
    max-width: r(416);
    color: $greyColor;

  }
}
